import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import About from '../views/About'
import Contact from '../views/Contact'
import Blog from '../views/Blog'
import BlogRead from '../views/BlogRead'
// import Particles from '../views/Particles'

import Maintenance from '../views/Maintenance'

import NotFound from '../views/Error_404'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: "Home"
    }
  },
  {
    path: '/about',
    name: 'About',
    component: About,
    meta: {
      title: "About"
    },
  },
  {
    path: '/skills',
    name: 'Skills',
    component: Maintenance,
    meta: {
      title: "Page is under Maintenance"
    }
  },
  {
    path: '/education',
    name: 'Education',
    component: Maintenance,
    meta: {
      title: "Page is under Maintenance"
    }
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact,
    meta: {
      title: "Get in Touch"
    },
  },
  {
    path: '/blog',
    name: 'Blog',
    component: Blog,
    meta: {
      title: 'Blog'
    }
  },
  {
    path: '/blog/:id',
    name: 'Blog Read',
    component: BlogRead,
    meta: {
      title: 'Blog'
    }
  },
  // {
  //   path: '/particles-js',
  //   name: 'Particles JS',
  //   component: Particles,
  //   meta: {
  //     title: 'Particles JS'
  //   }
  // },

  // 404
  {
    path: '*',
    name: 'NotFound',
    component: NotFound,
    meta: {
      title: "404 Page Not Found"
    },
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  document.title = `${to.meta.title} - febryars33`;
  next();
});

export default router
