        <!-- Right -->
        <div class="col-lg-8">
          <img
            src="https://images.unsplash.com/photo-1621839673705-6617adf9e890?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1032&q=80"
            class="img-fluid mb-5 rounded-3"
            alt="..."
          />
          <h3 class="fw-bold mb-3">{{ blog.title }}</h3>
          <p>{{ blog.body }}</p>
          <p>
            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Sed earum
            voluptatum deserunt! Modi, obcaecati fugiat, sed beatae veritatis
            quas reiciendis doloribus placeat tempora cum ducimus id quod unde
            cupiditate eos.
          </p>
          <p>
            Lorem ipsum dolor sit amet consectetur, adipisicing elit. Explicabo
            veniam nisi, ipsum error eum nobis quod laborum, dolores soluta
            pariatur excepturi cumque reiciendis aspernatur harum aliquid sed
            quas, culpa eveniet officiis dolorem molestiae. Dignissimos numquam
            aspernatur dolore blanditiis aut qui sunt. Quos esse optio, aliquam
