          <div class="card border-0 shadow mt-5 rounded-3">
            <div class="card-body">
              <h5 class="fw-bold card-title">{{ comments_count }} Comments</h5>
              <hr />
              <div class="mb-3">
                <textarea
                  name="comment_form"
                  id="comment_form"
                  class="form-control"
                  rows="10"
                  placeholder="Write comment here..."
                ></textarea>
              </div>
              <div class="mb-5">
                <div class="d-grid">
                  <button
                    type="button"
                    class="btn btn-primary border-0"
                    @click="commentFormClicked()"
                  >
