                  <img
                    src="
                      https://via.placeholder.com/150/FF0000/FFFFFF
                    "
                    alt="Profile"
                    class="img-fluid rounded-3"
                  />
                </div>
                <div class="col-lg">
                  <h5>{{ comment.name }}</h5>
                  <p class="small text-muted">
                    Jumat, 14 Januari 2022 | 08:00 WIB
                  </p>
                  <p>
