              >
                Programming
              </span>
              <span
                class="
                  badge
                  rounded-pill
                  bg-secondary
                  text-decoration-none
                  me-2
                "
              >
                RESTful API
              </span>
              <span
                class="badge rounded-pill bg-info text-decoration-none me-2"
              >
                Tutorial
              </span>
              <span
                class="badge rounded-pill bg-danger text-decoration-none me-2"
              >
                Laravel
              </span>
              <span
                class="badge rounded-pill bg-success text-decoration-none me-2"
              >
                Vue
              </span>
              <span
