      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "BlogRead",
  data() {
    return {
      blog: [],
      comments: [],
      comments_count: 0,
    };
  },
  methods: {
    setBlog(data) {
      this.blog = data;
    },
    setCountComments(data) {
      this.comments_count = data;
    },
    setComments(data) {
      this.comments = data;
    },
    commentFormClicked() {
      this.$toast.warning("This feature is under development.", {
        position: "top-right",
      });
    },
  },
  mounted() {
    // comments
    axios
      .get("https://jsonplaceholder.typicode.com/comments", {
        params: {
          _limit: 10,
        },
      })
      .then((response) => {
        this.setComments(response.data);
        this.setCountComments(Object.keys(response.data).length);
      })
      .catch((error) => {
        this.$toast.error(error + " (Comments)", {
          position: "top-right",
        });
      });

    // blog
    axios
      .get(
        "https://jsonplaceholder.typicode.com/posts/" + this.$route.params.id
      )
      .then((response) => {
        this.setBlog(response.data);
        document.title = response.data.title + " - febryars33";
      })
      .catch((error) => {
        this.$toast.error(error + " (Post)", {
          position: "top-right",
        });
      });
  },
};
</script>

