<template>
  <div class="py-5 bg-light">
    <div class="container">
      <div class="row">
        <!-- Right -->
        <div class="col-lg-8">
          <div
            class="card border-0 shadow mb-5"
            v-for="blog in blogs"
            :key="blog.id"
            data-aos="fade-in"
          >
            <router-link class="text-decoration-none" :to="'/blog/' + blog.id"
              ><img
                src="https://images.unsplash.com/photo-1621839673705-6617adf9e890?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1032&q=80"
                class="card-img-top"
                alt="..."
            /></router-link>

            <div class="card-body">
              <h5 class="card-title">
                <router-link
                  class="text-decoration-none"
                  :to="'/blog/' + blog.id"
                  >{{ blog.title }}</router-link
                >
              </h5>
              <p class="card-text">{{ blog.body }}</p>
              <p class="card-text">
                <small class="text-muted">Kamis, 13 Januari 2022</small>
              </p>
            </div>
          </div>
        </div>

        <!-- Left -->
        <div class="col-lg-4 order-first order-lg-last">
          <!-- Search -->
          <div
            data-aos="fade"
            data-aos-delay="300"
            class="card border-0 mb-4"
            style="background-color: #cff4fc"
          >
            <div class="card-body">
              <h5 class="fw-bold text-info">Search</h5>
              <div class="mt-4 mb-3">
                <input
                  type="text"
                  id="search"
                  class="form-control border-0"
                  placeholder="Search"
                />
              </div>
              <div class="d-grid">
                <button type="button" class="btn btn-outline-danger">
                  Search
                </button>
              </div>
            </div>
          </div>
          <!-- Subscribe -->
          <div
            data-aos="fade"
            data-aos-delay="300"
            class="card border-0 mb-4"
            style="background-color: #cfe2ff"
          >
            <div class="card-body">
              <h5 class="fw-bold text-primary">
                Subscribe blog for latest updates
              </h5>
              <div class="mt-4 mb-3">
                <input
                  type="email"
                  id="email"
                  class="form-control border-0"
                  placeholder="Email address"
                />
              </div>
              <div class="d-grid">
                <button type="button" class="btn btn-danger border-0">
                  Subscribe
                </button>
              </div>
            </div>
          </div>

          <!-- Tags -->
          <div
            data-aos="fade"
            data-aos-delay="300"
            class="card border-0 shadow-sm mb-5"
          >
            <div class="card-body">
              <h5 class="fw-bold text-danger mb-3">Tags</h5>
              <span
                class="badge rounded-pill bg-primary text-decoration-none me-2"
              >
                Programming
              </span>
              <span
                class="
                  badge
                  rounded-pill
                  bg-secondary
                  text-decoration-none
                  me-2
                "
              >
                RESTful API
              </span>
              <span
                class="badge rounded-pill bg-info text-decoration-none me-2"
              >
                Tutorial
              </span>
              <span
                class="badge rounded-pill bg-danger text-decoration-none me-2"
              >
                Laravel
              </span>
              <span
                class="badge rounded-pill bg-success text-decoration-none me-2"
              >
                Vue
              </span>
              <span
                class="badge rounded-pill bg-info text-decoration-none me-2"
              >
                React
              </span>
              <span
                class="badge rounded-pill bg-warning text-decoration-none me-2"
              >
                CodeIgniter
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Blogs",
  data() {
    return {
      blogs: [],
    };
  },
  methods: {
    setBlog(data) {
      this.blogs = data;
    },
  },
  mounted() {
    axios
      .get("https://jsonplaceholder.typicode.com/posts", {
        params: {
          _limit: 10,
        },
      })
      .then((response) => {
        this.setBlog(response.data);
        // console.log(Object.keys(response.data).length);
      })
      .catch((error) => {
        this.error = error;
      });
  },
};
</script>

